import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Helmet } from "react-helmet";
import clsx from 'clsx';
import {
    Card, CssBaseline, CardHeader, CardMedia, Grid, CardContent, FormControl, InputAdornment, FormHelperText, OutlinedInput, CardActions, Collapse
    , Avatar, Button, Typography, Container, Backdrop, CircularProgress, LinearProgress, TextField
} from '@material-ui/core';
import { red } from '@material-ui/core/colors';
import Alert from '@material-ui/lab/Alert';
import CaculateIcon from '@material-ui/icons/SendOutlined';
import ArrowDown from '@material-ui/icons/ArrowDropDownCircleOutlined';
import TrendingUp from '@material-ui/icons/TrendingUpOutlined';
import CustomSnakbar from '../../../Components/CustomSnakbar';
import TableCell from '@material-ui/core/TableCell';
import { List, ListItem, ListItemAvatar, ListItemText, TableRow } from '@material-ui/core';
import SubNavBar from '../../../Components/SubNavBar';

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: '#1a1a1af0',
        borderRadius: '3px 3px 0px 0px',
        color: theme.palette.common.white,
    },
    body: {
        fontSize: 14,
        width: '35%',
        border: '0.1rem solid #e2e2e2',
        textAlign: 'left'
    }
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        overflow: 'hidden'
    },
}))(TableRow);


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '99%',
        backgroundColor: 'white'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    control: {
        padding: theme.spacing(1),
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
    button: {
        height: 40,
        minWidth: "175px",
        background:
            "transparent linear-gradient(180deg, rgb(0 85 255) 0%, #1962BF 100%) 0% 0% no-repeat padding-box",
        fontSize: 15,
        color: "white",
        marginTop: 14,
        padding: '26px',
        position: "relative",
        [theme.breakpoints.down("xs")]: {
            minWidth: "146px",
            fontSize: "13px",
        },
    },
    gridItem: {
        margin: '10px 10px 20px 15px',
        minWidth: '252px'
    },
    margin: {
        margin: theme.spacing(1),
    },
}));


export default function Bmi() {

    const classes = useStyles();
    const [snakOpen, setSnakOpen] = React.useState(null);
    const [snakMessage, setSnakMessage] = React.useState(null);

    const [state, setState] = React.useState({
        weight: '',
        height: '',
        bmi: 0,
        seviority: '',
        message: '',
        showResult: false,
    });

    const onSubmitClick = (e) => {

        if (state.height.length === 0) {
            setSnakOpen(true);
            setSnakMessage('Enter height value !');
        } else if (state.weight.length === 0) {
            setSnakOpen(true);
            setSnakMessage('Enter weight value !');
        } else {
            let fltbmi = ((state.weight * 10000) / (state.height * state.height)).toPrecision(4);
            setState({
                ...state,
                bmi: fltbmi,
                showResult: 'true',
                message: fltbmi < 18.5 ? 'You are Under weight' : (
                    fltbmi >= 18.5 && fltbmi <= 24.9 ? 'You are normal weight' : (
                        fltbmi >= 2.5 && fltbmi <= 29.9 ? 'You are over weight' : 'oh !. You have obesity problem.'
                    )
                ),
                seviority: fltbmi < 18.5 ? 'info' : (
                    fltbmi >= 18.5 && fltbmi <= 24.9 ? 'success' : (
                        fltbmi >= 2.5 && fltbmi <= 29.9 ? 'warning' : 'error'
                    )
                ),
            })
            SetBackDropopen(true);
            setTimeout(() => {
                SetBackDropopen(false);
            }, 250);
        }
    };

    const handleClose = (e) => {
        setSnakOpen(null);
        setSnakMessage(null);
    };

    const [backDropopen, SetBackDropopen] = React.useState(false);

    return (
        <div className={classes.root}>
            <CustomSnakbar
                open={snakOpen}
                msg={snakMessage}
                handleClose={handleClose}
            />
            <Backdrop className={classes.backdrop} open={backDropopen}>
                <CircularProgress color="inherit" />
            </Backdrop>
            <Helmet>
                <title>Body Mass Index (BMI) calculator || Calculate your Body Mass Index - Math Calc</title>
            </Helmet>
            <SubNavBar />
            <div class='appHeading'>
                <h4 class='my-4'><strong>BMI calculator</strong></h4>
            </div>
            <Container maxWidth={'md'} style={{ paddingLeft: '0px', paddingRight: '0px' }}>
                <Card className={classes.root} raised elevation={0} >
                    <CardContent class='appContainer'>
                        <p className={'text-muted'} >
                            Body mass index (BMI) is a measure of body fat based on height and weight that applies to adult men and women.

                            Enter your weight and height using standard or metric measures.
                            Select "Compute BMI" and your BMI will appear below.
                        </p>
                        <Grid container justify="space-around">
                            <TextField id="outlined-basic" style={{ display: 'none' }} label="Outlined" variant="outlined" />
                            <TextField
                                label="Height in cm"
                                id="height"
                                className={classes.gridItem}
                                variant="outlined"
                                value={state.height}
                                maxLength={'7'}
                                onChange={(e) => {
                                    if (!isNaN(e.target.value) && e.currentTarget.value.length < 7) {
                                        setSnakOpen(null);
                                        setSnakMessage(null);
                                        setState({
                                            ...state, height: e.target.value
                                        });
                                    } else {
                                        setSnakOpen(null);
                                        setSnakMessage(null);
                                        return
                                    }
                                }}
                            />
                            <TextField
                                label="Weight in kgs"
                                id="weight"
                                className={classes.gridItem}
                                variant="outlined"
                                value={state.weight}
                                maxLength={'7'}
                                onChange={(e) => {
                                    if (!isNaN(e.target.value) && e.currentTarget.value.length < 7) {
                                        setSnakOpen(null);
                                        setSnakMessage(null);
                                        setState({
                                            ...state, weight: e.target.value
                                        });
                                    } else {
                                        setSnakOpen(null);
                                        setSnakMessage(null);
                                        return
                                    }
                                }}
                            />
                            <Button
                                variant="contained"
                                color="white"
                                className={classes.button + ' ' + classes.gridItem}
                                startIcon={<CaculateIcon />}
                                onClick={() => { onSubmitClick() }}
                            >Compute BMI</Button>
                        </Grid>
                    </CardContent>
                    <Collapse in={true} timeout="auto" unmountOnExit>
                        <CardContent>
                            <div class='div-card' style={{
                                display: state.showResult ? 'block' : 'none', border: '0px',
                                background: 'linear-gradient(to right, rgba(49,207,171,0.5), #50e3c2)',
                                transition: 'all 250ms ease-out',
                                transitionDelay: '50ms',
                                marginBottom: '1rem'
                            }}>
                                <Typography paragraph class='my-4 header-left' ><strong>Result:
                                                              </strong> &nbsp; Your BMI is {state.bmi}
                                </Typography>
                                <Alert elevation={6} variant="filled" severity={state.seviority}>
                                    {state.message}
                                </Alert>
                            </div>
                            <div class='div-card' style={{ backgroundColor: '#e5a400', width: '100%' }}>
                                <Typography variant="h6" style={{ fontWeight: '800', fontSize: '1.5rem' }}>
                                    BMI Categories
                                </Typography>
                                <div style={{ color: 'white' }} >
                                    <List >
                                        <ListItem>
                                            <ListItemAvatar>
                                                <Avatar className={classes.avatar}>
                                                    <TrendingUp />
                                                </Avatar>
                                            </ListItemAvatar>
                                            <ListItemText
                                                primary="Underweight <= 18.5"
                                            />
                                        </ListItem>  <ListItem>
                                            <ListItemAvatar>
                                                <Avatar className={classes.avatar}>
                                                    <TrendingUp />
                                                </Avatar>
                                            </ListItemAvatar>
                                            <ListItemText
                                                primary="Normal weight = 18.5–24.9"
                                            />
                                        </ListItem>
                                        <ListItem>
                                            <ListItemAvatar>
                                                <Avatar className={classes.avatar}>
                                                    <TrendingUp />
                                                </Avatar>
                                            </ListItemAvatar>
                                            <ListItemText
                                                primary=" Overweight = 25–29.9
                                                Obesity = BMI of 30 or greater"
                                            />
                                        </ListItem>
                                    </List>
                                </div>


                            </div>

                            <Typography paragraph class='my-4 header-left' ><strong>Who shouldn't use a BMI calculator</strong></Typography>
                            <p className={'text-muted header-left'} >  BMI is not used for muscle builders, long distance athletes, pregnant women, the elderly or young children. This is because BMI does not take into account whether the weight is carried as muscle or fat, just the number. Those with a higher muscle mass, such as athletes, may have a high BMI but not be at greater health risk. Those with a lower muscle mass, such as children who have not completed their growth or the elderly who may be losing some muscle mass may have a lower BMI.
                            During pregnancy and lactation, a woman's body composition changes, so using BMI is not appropriate.
                    </p>
                        </CardContent>
                    </Collapse>
                </Card>
            </Container>
        </div >
    );
}
