import React from "react";
import { BrowserRouter, Route, Switch, HashRouter, Redirect, useHistory, useLocation, } from "react-router-dom";
import { createBrowserHistory } from "history";
import './App.css';
import Header from './Components/Header';
import Footer from './Components/Footer';
import Home from './Pages/Home/Home';
import Commingsoon from './Components/Commingsoon';
import Calculator from './Pages/Calculator/Calculator';
import Scientific from './Pages/Scientific/Sceintific';
import Finance from './Pages/Finance/Finance';
import Algebric from './Pages/Algebric/Algebric';
import Formulae from "./Pages/Algebric/apps/Formulae";
import General from "./Pages/General/General";
import Age from "./Pages/General/apps/Age";
import Bmi from "./Pages/General/apps/Bmi";
import AllTools from "./Pages/General/AllTools";
import Share from "./Components/Share";
import FileNotFound from "./Components/FileNotFound";
import SimpleInterest from "./Pages/Finance/Apps/SimpleInterest";
import CompoundInterest from "./Pages/Finance/Apps/CompoundInterest";
function App() {
  const history = createBrowserHistory();
  let currentPath = history.location.pathname;
  if (currentPath == "/") {
    currentPath = "Home";
  }

  return (
    <div className="App">
      <BrowserRouter>
        {/* Common Pages for All roles */}
        <Header />
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/general" component={General} />
          <Route exact path="/all-calculators" component={AllTools} />
          <Route exact path="/general/age-calculator" component={Age} />
          <Route exact path="/general/bmi-calculator" component={Bmi} />
          <Route exact path="/scientific" component={Scientific} />

          <Route exact path="/finance" component={Finance} />
          <Route exact path="/finance/simple-interest" component={SimpleInterest} />
          <Route exact path="/finance/compound-interest" component={CompoundInterest} />

          {/* routes for algebric */}
          <Route exact path="/algebric" component={Algebric} />

          <Route exact path="/algebric/formulae" component={Formulae} />
          {/* routes for formulas */}
          <Route exact path="/algebric/formulae/age-calculator" component={Algebric} />

          <Route exact path="/algebric/equations" component={Algebric} />
          <Route exact path="/algebric/concepts" component={Algebric} />
          <Route exact path="/algebric/graphs" component={Algebric} />

          <Route exact path="/about" component={Commingsoon} />
          <Route exact path="/share" component={Share} />
          <Route exact path="/feedbacks" component={Commingsoon} />
          <Route path="/" component={FileNotFound} />
        </Switch>
        <Footer />
      </BrowserRouter>
    </div>
  );
}

export default App;
