import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Box, Divider, Container, Grid, Button, InputBase } from '@material-ui/core';
import Share from "./Share";
import { ArrowRightAlt } from '@material-ui/icons';
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: '#bfbfbf',
    color: 'white',
    bottom: '0',
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  footerLink: {
    color: 'white !important',
    textDecoration: 'none !important',
    listStyle: 'none',
    padding: '20px 20px 20px 0px',
    display: 'grid',
    cursor: 'pointer',
    "&hover:": {
      color: 'blue',
    },
  },
  footer: {
    boxSizing: 'border-box',
    color: '#fff;',
    padding: '0',
    textAlign: 'center'
  },
  ul: {
    display: 'flex',
    justifyContent: 'center'
  }
}));

export default function Footer() {
  const classes = useStyles();

  return (
    <div className={classes.root} >
      <Container className={classes.footer}>
        <ul className={classes.ul}>
          <li className={classes.footerLink}>
            <a href='/'>
              © 2021 MathCalc, Inc</a> </li>
          <li className={classes.footerLink}><a href='/home'>About</a> </li>
          <li className={classes.footerLink}><a href='/feedback' >Feedback</a> </li>
        </ul>
      </Container>
    </div >
  );
}
