import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Helmet } from "react-helmet";
import clsx from 'clsx';
import {
    Card, CssBaseline, CardHeader, CardMedia, Grid, CardContent, CardActions, Collapse
    , Avatar, Button, Typography, Container, Backdrop, CircularProgress, LinearProgress
} from '@material-ui/core';
import { red } from '@material-ui/core/colors';
import CaculateIcon from '@material-ui/icons/SendOutlined';
import ArrowDown from '@material-ui/icons/ArrowDropDownCircleOutlined';
import MomentUtils from '@date-io/moment';
import {
    MuiPickersUtilsProvider,
    DatePicker,
} from '@material-ui/pickers';
import CustomSnakbar from '../../../Components/CustomSnakbar';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import SubNavBar from '../../../Components/SubNavBar';

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: 'darkcyan',
        borderRadius: '3px 3px 0px 0px',
        color: theme.palette.common.white,
    },
    body: {
        fontSize: 14,
        width: '35%',
        border: '0.1rem solid #e2e2e2',
        textAlign: 'left'
    }
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        overflow: 'hidden'
    },
}))(TableRow);


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '99%',
        backgroundColor: 'transparent'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    control: {
        padding: theme.spacing(1),
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
    button: {
        background: " linear-gradient(180deg, #212121 0%, #010101 100%) 0% 0% ",
        height: 50,
        width: 202,
        borderRadius: 60,
        "&:hover": {
            background: " linear-gradient(180deg, #010101 0%, #212121 100%) 0% 0% ",
        },
        backgroundColor: '#212121 !important'
    },
    gridItem: {
        margin: '10px 10px 20px 15px',
        minWidth: '252px'
    }
}));


export default function Age() {

    const classes = useStyles();
    const [progress, setProgress] = React.useState(0);
    const [buffer, setBuffer] = React.useState(10);

    const progressRef = React.useRef(() => { });
    React.useEffect(() => {
        progressRef.current = () => {
            if (progress > 100) {
                setProgress(0);
                setBuffer(10);
            } else {
                const diff = Math.random() * 10;
                const diff2 = Math.random() * 10;
                setProgress(progress + diff);
                setBuffer(progress + diff + diff2);
            }
        };
    });

    React.useEffect(() => {
        const timer = setInterval(() => {
            progressRef.current();
        }, 500);

        return () => {
            clearInterval(timer);
        };
    }, []);

    const [snakOpen, setSnakOpen] = React.useState(null);
    const [snakMessage, setSnakMessage] = React.useState(null);
    const [selectedFrom, setSelectedFrom] = React.useState(new Date().toLocaleDateString().substr(0, 9));
    const [selectedTo, setSelectedTo] = React.useState(new Date().toLocaleDateString().substr(0, 9));

    const [dateDiff, SetDateDiff] = React.useState(0);
    const [nextBirthDay, SetnextBirthDay] = React.useState('');
    const [countDown, SetcountDown] = React.useState('');

    const onCalculateClick = (e) => {
        SetBackDropopen(true);
        const diffTime = Math.abs((new Date(selectedTo)) - (new Date(selectedFrom)));
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        SetDateDiff(diffTime);

        debugger;

        let todate = new Date();
        let birthday = 0;

        if ((new Date(selectedFrom)).getMonth() < todate.getMonth() && (new Date(selectedFrom)).getDate() < todate.getDate()) {
            SetcountDown((new Date(todate.getFullYear() + '-' + new Date(selectedFrom).getMonth() + '-' + new Date(selectedFrom).getDate()) - new Date()));
            birthday = (new Date(todate.getFullYear() + '-' + new Date(selectedFrom).getMonth() + '-' + new Date(selectedFrom).getDate()).getDay());
        }
        else {
            birthday = new Date((todate.getFullYear() + 1) + '-' + new Date(selectedFrom).getMonth() + '-' + new Date(selectedFrom).getDate()).getDay();
            SetcountDown((new Date((todate.getFullYear() + 1) + '-' + new Date(selectedFrom).getMonth() + '-' + new Date(selectedFrom).getDate()) - new Date()));
        }
        setInterval(() => {
            SetcountDown(countDown - 1);
        }, 1000);
        switch (birthday) {
            case 0:
                SetnextBirthDay('Sunday');
                break;
            case 1:
                SetnextBirthDay('Monday');
                break;
            case 2:
                SetnextBirthDay('Tuesday');
                break;
            case 3:
                SetnextBirthDay('Wednesday');
                break;
            case 4:
                SetnextBirthDay('Thursday');
                break;
            case 5:
                SetnextBirthDay('Friday');
                break;
            case 6:
                SetnextBirthDay('Saturday');
                break;
            default:
                break;
        }
        setTimeout(() => {
            SetBackDropopen(false);
        }, 250);
    };

    const handleClose = (e) => {
        setSnakOpen(null);
        setSnakMessage(null);
    };

    const [backDropopen, SetBackDropopen] = React.useState(false);

    return (
        <div className={classes.root}>
            <CustomSnakbar
                open={snakOpen}
                msg={snakMessage}
                handleClose={handleClose}
            />
            <Backdrop className={classes.backdrop} open={backDropopen}>
                <CircularProgress color="inherit" />
            </Backdrop>
            <Helmet>
                <meta charSet="utf-8" />
                <title>Age calculator - Math Calc</title>
            </Helmet>
            <SubNavBar />
            <div class='appHeading'>
                <h4 class='my-4'><strong> Age calculator</strong></h4>
            </div>
            <Container maxWidth={'md'} style={{ paddingLeft: '0px', paddingRight: '0px' }}>
                <Card className={classes.root} raised elevation={0} >
                    <h4 class='my-4'><strong><ArrowDown /> Modify the values and click the calculate button to use
                </strong></h4>   <CardContent class='appContainer'>
                        <p className={'text-muted'} >
                            The Age Calculator can determine the age or interval between two dates.
                            The calculated age will be displayed in years, months, weeks, days, hours, minutes, and seconds
                            </p>
                        <MuiPickersUtilsProvider utils={MomentUtils}>
                            <Grid container justify="space-around">
                                <DatePicker
                                    label="Date of Birth"
                                    inputVariant="outlined"
                                    id="selectedFrom"
                                    value={selectedFrom}
                                    openTo="year"
                                    views={["year", "month", "date"]}
                                    format='DD-MM-yyyy'
                                    onChange={(e) => {
                                        SetDateDiff(0);
                                        if (new Date(e) > new Date(selectedTo)) {
                                            setSnakOpen(true)
                                            setSelectedFrom(new Date().toLocaleDateString().substr(0, 9));
                                            setSnakMessage('Choose Date of birth less than the age at the date of');
                                        }
                                        else { setSelectedFrom(e); }
                                    }}
                                    className={classes.gridItem}
                                />
                                <DatePicker
                                    label="Age at the Date of"
                                    inputVariant="outlined"
                                    value={selectedTo}
                                    id="selectedTo"
                                    openTo="year"
                                    views={["year", "month", "date"]}
                                    format='DD-MM-yyyy'
                                    onChange={(e) => {
                                        SetDateDiff(0);
                                        if (new Date(e) < new Date(selectedFrom)) {
                                            setSnakOpen(true)
                                            setSelectedTo(new Date().toLocaleDateString().substr(0, 9));
                                            setSnakMessage('Choose Date of birth less than the age at the date of');
                                        }
                                        else { setSelectedTo(e); }
                                    }}
                                    className={classes.gridItem}
                                />
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    className={classes.button + ' ' + classes.gridItem}
                                    startIcon={<CaculateIcon />}
                                    onClick={() => { onCalculateClick() }}
                                >Calculate</Button>
                            </Grid>
                        </MuiPickersUtilsProvider>
                    </CardContent>
                    <Collapse in={true} timeout="auto" unmountOnExit>
                        <CardContent>
                            <Typography paragraph><strong>Result:</strong></Typography>
                            <TableContainer style={{ overflow: 'hidden' }}>
                                <Table className={classes.table} aria-label="customized table">
                                    <TableHead>
                                        <TableRow >
                                            <StyledTableCell colSpan={2}>Age Calculation</StyledTableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        <StyledTableRow >
                                            <StyledTableCell >
                                                Your Age is
                                                </StyledTableCell>
                                            <StyledTableCell align="right">{Math.floor(dateDiff / (1000 * 60 * 60 * 24 * 365)) + ' years '
                                                + Math.floor(
                                                    (dateDiff % (1000 * 60 * 60 * 24 * 365)) / (1000 * 60 * 60 * 24 * 30)
                                                ) + ' Months, and '
                                                + Math.ceil((((dateDiff % (1000 * 60 * 60 * 24 * 365)) / (1000 * 60 * 60 * 24 * 7)) % (1000 * 60 * 60 * 24 * 7)) / (1000 * 60 * 60 * 24)) + ' Days'
                                            }</StyledTableCell>
                                        </StyledTableRow>
                                        <StyledTableRow >
                                            <StyledTableCell >
                                                Your Age in weeks
                                                </StyledTableCell>
                                            <StyledTableCell align="right">{
                                                Math.floor(dateDiff / (1000 * 60 * 60 * 24 * 7)) + ' weeks '
                                                + (dateDiff % (1000 * 60 * 60 * 24 * 7)) / (1000 * 60 * 60 * 24) + ' Days'
                                            }</StyledTableCell>
                                        </StyledTableRow>
                                        <StyledTableRow >
                                            <StyledTableCell >
                                                Your Age Days
                                                </StyledTableCell>
                                            <StyledTableCell align="right">{dateDiff / (1000 * 60 * 60 * 24) + ' Days'}</StyledTableCell>
                                        </StyledTableRow>
                                        <StyledTableRow >
                                            <StyledTableCell >
                                                Your Age in Hours
                                                </StyledTableCell>
                                            <StyledTableCell align="right">{dateDiff / (1000 * 60 * 60) + ' Hours'}</StyledTableCell>
                                        </StyledTableRow>
                                        <StyledTableRow >
                                            <StyledTableCell >
                                                Your Age in Minutes
                                                </StyledTableCell>
                                            <StyledTableCell align="right">{dateDiff / (1000 * 60) + ' Minutes'}</StyledTableCell>
                                        </StyledTableRow>
                                        <StyledTableRow >
                                            <StyledTableCell >
                                                Your Age in Seconds
                                                </StyledTableCell>
                                            <StyledTableCell align="right">{dateDiff / 1000 + ' Seconds'}</StyledTableCell>
                                        </StyledTableRow>
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </CardContent>
                    </Collapse>
                </Card>

            </Container>
        </div >
    );
}
