import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { CssBaseline, Container, Typography, Grid, Card, IconButton } from '@material-ui/core';
import Compress from '../../Assets/icons/Compress';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        overflow: 'hidden'
    },
    control: {
        padding: theme.spacing(2),
    },
    divcalc: {
        color: '#744cbc',
        textDecoration: 'none',
        border: '1px solid #e6ecf1',
        margin: '10px',
        padding: '16px',
        boxSizing: 'border-box',
        cursor: 'pointer',
        position: 'relative',
        display: 'inline-flex',
        flexDirection: 'row',
        alignItems: 'center',
        placeSelf: 'stretch',
        color: '#242a31',
        backgroundColor: '#fff',
        borderRadius: '3px',
        boxShadow: '0 3px 8px 0 rgb(216 216 216)',
        minWidth: '300px',
        maxWidth: '100%',
        minHeight:'100px'
    }
}));

export default function Finance() {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <CssBaseline />
            <section style={{
                width: '100%', paddingLeft: '2rem', paddingRight: '2rem', paddingTop: '2rem', paddingBottom: '0.5rem',
                margin: 'auto',
            }}>
                <h1 style={{
                    display: 'block', fontSize: '26px', lineHeight: '1.2', fontWeight: '800', marginTop: '0px',
                    textTransform: 'capitalize', textAlign: 'center', marginBottom: '1rem',
                }}>Financial calculator</h1>
            </section>
            <Container maxWidth={'md'} style={{ paddingLeft: '0px', paddingRight: '0px', display: 'grid', alignItems: 'center' }}>
                <div style={{
                    display: 'table-row-group',
                    placeItems: 'center',
                    textAlign: 'center',
                    margin: '30px'
                }}>
                    <div>
                        <a href='/finance/simple-interest' className={classes.divcalc}>
                            <Compress style={{
                                width: '30px',
                                margin: '0px 8px 0px 0px'
                            }} /> <span>Simple Interest</span></a>
                        <a href='/finance/compound-interest' className={classes.divcalc}>
                            <Compress style={{
                                width: '30px',
                                margin: '0px 8px 0px 0px'
                            }} /> <span>Copound Interest</span></a>
                    </div>
                </div>
            </Container>
        </div >
    );
}
